@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

.wrapper {
    height: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.header,
.footer {
    flex-shrink: 0;
}
.main {
    flex-grow: 1;
}
.container {
    max-width: 1143px;
    width: 100%;
    box-sizing: border-box;
    margin: 0 auto;
}

.transparent-slider-btn {
    background: transparent;
    border: none;
    outline: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 41px;
    cursor: pointer;
    z-index: 100;

    &:last-child {
        right: 0;
        transform: scale(-1) translateY(50%);
    }
}

.--main-color {
    color: $main-color;
}

.page-header {
    @include page-header();
}
.page-subheader {
    @include page-subheader();
}
.page-text {
    @include page-text();
}
.page-field {
    @include page-field();
}
.page-btn {
    @include page-btn();
}
.page-btn-transparent {
    @include page-btn-transparent();
}
.--dropdown-arrow {
    &:after {
        content: '';
        transition: .3s transform;
        border: 4px solid transparent; 
        border-top: 4px solid #fff;
        width: 8px;
        display: block;
        box-sizing: border-box;
        position: relative;
        top: 2px;
    }
}

* {
    font-family: 'Myriad Pro' !important;
    min-height: 0;
    min-width: 0;
    letter-spacing: 0.3px;    
}

.header {
    position: relative;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    // display: none;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    .container {

    }
    .header__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }

    .header__top {
        background: #343B40;
        padding: 13px 0;
        box-sizing: border-box;
    }
    .logo {
        display: inline-block;
        border: 2px solid #FFFFFF;    
        img {
            display: block;
            width: 100%;
        }    
    }
    .header__text {
        font-family: 'Myriad Pro';
        // font-weight: 400;
        font-weight: 300;
        font-size: 18px;
        line-height: 140%;
        /* or 25px */
        text-align: center;
        color: #FFFFFF;
        max-width: 253px;
    }
    .header__controlls {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
    }
    .header__phone {
        font-style: normal;
        font-weight: 600;
        font-size: 32px;
        line-height: 140%;
        
        color: #FFFFFF;
        text-decoration: none;
    }
    .header__socials {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        gap: 2px;
    }
    .header__social-item {
        img {
            width: 23px;
            height: 23px;
        }
    }
    .header__btn {
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
    }
    .header__auth {
        transition: color .3s;
        display: flex;
        justify-content: center;
        gap: 7px;
        text-decoration: none;
        color: #FFFFFF;
        svg {
            width: 19px;
            height: 19px;
        }
        span {
            font-weight: 400;
            font-size: 15px;
            line-height: 18px;
            
            color: #FFFFFF;
            transition: color .3s;

        }

        &:hover {
            color: $main-color;
            span {
                color: $main-color;
            }
        }
    }

    .header__bottom {
        background: #2C3236;
    }
    .header__nav {
        flex-grow: 1;
        display: flex;
        align-items: center;
    }
    .header__list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        flex-shrink: 0;
        width: 100%;
    }
    .header__item {
        padding: 15px 0;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        border-bottom: 3px solid #252B2F;  
        transition: background .3s, border-bottom .3s; 
        cursor: pointer;        

        color: #fff;
        svg {
            width: 19px;
            height: 19px;
        }
        .header__item-link {
            text-transform: uppercase;
            text-decoration: none;
            color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 10px;

            font-weight: 600;
            font-size: 15px;
            line-height: 140%;            
        }

        &:hover {
            background: #252B2F; 
            border-bottom: 3px solid #B34215;
        }
    }
    .header__search-wrap {
        
    }
    .header__search {
        color: #fff;
        svg {
            width: 18px;
            height: 18px;
        }
    }
    .header__search-wrapper {
        display: none;
        position: absolute;
        top: 100%;
        right: 0;

        background: #252B2F; 
        padding: 10px;
        box-sizing: border-box;    
        z-index: 10;   
    }
    .header__search-form {
        svg {
            color: #fff;
            position: absolute;
            top: 24px;
            left: 19px;

            width: 20px;
            height: 20px;
        }
    }
    .header__search-input {
        padding: 8px 20px 8px 41px;
        box-sizing: border-box;
        border: 1px solid #FFFFFF;
        outline: none;
        background: transparent;

        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;        
    }
    .header__search-close {
        width: 16px;
        height: 16px;
        position: absolute;
        top: 25px;
        right: 24px;
        box-sizing: border-box;

        &::before,
        &::after {
            content: '';
            width: 2px;
            height: 100%;
            display: block;
            background: #fff;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%) rotate(45deg);
        }
        &::after {
            transform: translateX(-50%) rotate(-45deg);
        }
    }
    .header__search__list {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;

        padding: 11px 10px;
        box-sizing: border-box;

        background: #FFFFFF;
    }
    .header__search__list-item {
        font-weight: 400;
        font-size: 15px;
        line-height: 170%;
    }
    [data-hover-dd]:hover {
        [data-hover-list] {
            opacity: 1;
            pointer-events: all;
        }
    }
    .header__dropdown {
        transition: opacity .3s;
        opacity: 0;
        pointer-events: none;
        z-index: 10;

        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        padding: 30px 20px;
        box-sizing: border-box;
        background: #252B2F;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1); 
        
        display: flex;
        justify-content: space-between;
    }
    .header__dropdown-wrapper {
        ul:not(:first-child) {
            margin-top: 30px;
        }
    }
    .header__dropdown-list {

        * {
            color:  #fff;
        }
    }
    .header__dropdown-header {
        display: block;
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
        /* identical to box height, or 28px */
        color: #FFFFFF;
        margin-bottom: 13px;        
    }
    .header__dropdown-item {
        a {
            // font-weight: 400;
            font-weight: 300;
            font-size: 17px;
            line-height: 170%;
            
            color: #FFFFFF;
        }
    }

    .burger__wrapper {
        display: none;
    }
    .burger {
        cursor: pointer;

        width: 25px;
        height: 17px;

        display: flex;
        justify-content: center;
        position: relative;

        &::before,
        &::after {
            content: '';
            width: 80%;
            display: block;

            background: #fff;
            height: 2px;
            border-radius: 3px;

            position: absolute;
            top: 0;
            left: 0;
        }
        &::after {
            content: '';
            top: unset;
            bottom: 0;
        }

    }
    .burger__item {
        height: 2px;
        width: 100%;
        border-radius: 3px;

        background: #fff;
        margin: auto 0;
    }

    .header__mobile {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 100;
        background: #252B2F;

        padding: 30px 20px;
        box-sizing: border-box;
        height: 100vh;
        overflow-y: auto;

        .header__phone {
            font-weight: 600;
            font-size: 25px;
            line-height: 140%;
            text-align: center;
            display: block;
        }
        .header__btn {
            display: block;
            margin: 20px auto;
        }
        .header__socials {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: row;
            gap: 13px;
        }    
        .header__social-item img {
            width: 30px;
            height: 30px;
        }    
    }
    .header__mobile__close {
        display: block;
        position: absolute;
        top: 30px;
        right: 20px;

        width: 20px;
        height: 20px;

        &::before, &::after {
            content: '';
            width: 3px;
            border-radius: 3px;
            height: 100%;
            background: #fff;
            display: block;

            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &::after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
    .header__mobile-wrapper {

    }
    .header__mobile__section {

        &:not(:last-child) {
            margin-bottom: 25px;
        }
        &:last-of-type {
            padding-top: 20px;
            border-top: 1px solid rgba(255, 255, 255, .1);
        }
    }
    .header__mobile__lk {
        svg {
            width: 19px;
            height: 19px;
        }
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 7px;
        color: #fff;
        text-decoration: none;
        margin-bottom: 16px;
    }
    .header__mobile__search {
        position: relative;
        input {
            max-width: 100%;
            width: 100%;
        }
        button {
            position: absolute;
            top: 55%;
            transform: translateY(-50%);
            left: 8px;
            color: #fff;
            background: transparent;
            border: none;
            padding: 0;
            svg {
                width: 20px;
                height: 20px;
            }
        }
    }
    .header__mobile__list {

    }
    .header__mobile__item {

    }
    .header__mobile__item__text {
        color: #fff;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 140%;
        text-transform: uppercase;
        color: #FFFFFF;   
        text-decoration: none; 
        
        display: flex;
        justify-content: start;
        align-items: center;

        &.--dropdown-arrow::after {
            margin-left: auto;
            top: 0;
        }
    }
    .header__mobile__item__sublist {

    }
    .header__mobile__item__subitem {

        &:not(:last-child) {
            margin-bottom: 10px;
        }
        & .header__mobile__item__subitem:not(:last-child) {
            margin-bottom: 5px;
        }
        & .header__mobile__item__subitem .header__mobile__item__text {
            padding-top: 0;
            padding-bottom: 0;
            font-weight: 400;
            font-size: 13px;
        }
        // .header__mobile__item__sublist {
        //     padding-left: 10px;
        //     box-sizing: border-box;
        // }
        .header__mobile__item__text {
            text-transform: initial;
            padding: 5px 0;
        }
    }

    [data-acc-trigger] {
        cursor: pointer;
        padding: 15px 0;
        box-sizing: border-box;
        svg {
           width: 15px;
           height: 16px; 
           margin-right: 12px;
           flex-shrink: 0;
        }
    }
    [data-acc-trigger]._active.--dropdown-arrow::after {
        transform: rotate(180deg);
    } 
    [data-acc-list] {
        display: none;
    }


}

.index-page {

    .index-title {
     
        .index-title__slider-wrapper {
            position: relative;
        }
        .index-title__slider {

        }
        .index-title__slide {
            position: relative;
            min-height: 100vh;
            display: flex;
        }
        .index-title__bg {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: block;
            &._mob {
                display: none;
            }
        }
        .container {
            position: relative;
            z-index: 1;
            margin-top: auto;
        }
        .index-title__text {
            padding: 30px;
            box-sizing: border-box;
            max-width: 718px;
            min-height: 345px;

            display: flex;
            flex-direction: column;

            // background: rgba(55, 56, 61, 0.3);
            // backdrop-filter: blur(10px);

            background: rgba(55, 56, 61, 0.3);
            opacity: 0.97;
            backdrop-filter: blur(50px);
        }
        .index-title__header {
            text-transform: uppercase;
            font-style: normal;
            font-weight: 700;
            font-size: 28px;
            line-height: 140%;
            margin-bottom: 10px;
            color: #fff;
        }
        .index-title__desc {
            // font-weight: 400;
            font-weight: 300;
            font-size: 20px;
            line-height: 140%;
            margin-bottom: 20px;
            color: #fff;
        }
        .index-title__btns {
            margin-top: auto;
            display: flex;
            gap: 15px;
        }
        .index-title__btn {

        }
        .index-title__slider__dots-wrapper {
            position: absolute;
            bottom: 28px;
            right: 43%;
            z-index: 2;

            .slick-dots {
                position: static;
            }
        }
        .slick-dotted.slick-slider {
            margin-bottom: 0;
        }
        .slick-dots li {
            margin: 0;
        }
        // .slick-dots li button:hover:before, .slick-dots li button:focus:before {
        //     background: $main-color;
        // }

        .slick-dots li.slick-active button::before {
            background: $main-color;
        }
        .slick-dots li button::before {
            content: '';
            width: 9px;
            height: 9px;
            display: block;
            background: #EAEAEA;
            position: static;
            opacity: 1;
        }
    }

    .index-about {
        padding-top: 100px;
        padding-bottom: 100px;
        .container {
            
        }
        .index-about__wrapper {

        }
        .index-about__info {

        }
        .index-about__header {
            text-transform: uppercase;
            max-width: 850px;
            width: 100%;
            margin-bottom: 17px;
        }
        .index-about__list {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 26px;
        }
        .index-about__item {
            box-sizing: border-box;
            padding-top: 19px;
            padding-bottom: 22px;
            &:nth-child(2) {
                padding-left: 41px;
                padding-right: 41px;
                border-left: 1px solid rgba(217, 217, 217, .5);
                border-right: 1px solid rgba(217, 217, 217, .5);
            }
            &:last-of-type {
                position: relative;
                background: $main-color;
                padding: 19px 31px 22px;
                * {
                    color: #fff;
                }
            }
        }
        .index-about__num {
            font-weight: 700;
            font-size: 64px;
            line-height: 140%;
            display: block;
        }
        .index-about__desc {
            display: block;
            // max-width: 235px;
            max-width: 170px;
            text-transform: uppercase;            
        }
        .index-about__img {
            position: absolute;
            bottom: 100%;
            left: 0;
            width: 100%;
            height: 143px;
        }
        .index-about__bg {
            position: absolute;
            top: 50%;
            right: 14px;

            transform: translateY(-50%);
        }

        .index-about__bottom {
            display: grid;
            grid-template-columns: 1fr 1fr;
            align-items: center;
            margin-top: 100px;
        }
        .index-about__video {
            img {
                display: block;
                width: 100%;
                height: 400px;
                object-fit: cover;
            }
        }
        .index-about__text {
            box-sizing: border-box;
            padding-left: 47px;
            p {
                font-weight: 300;
                font-size: 18px;
                line-height: 140%;
                color: #343B40;                
            }
        }
        .index-about__subhead {
            margin-bottom: 10px;
        }
        .index-about__btn {
            margin-top: 30px;
        }
    }

    .main-projects {

        padding: 74px 0 80px;
        box-sizing: border-box;
        background: #F9F9F9; 

        .container {

        }
        .main-projects__wrapper {

        }
        .main-projects__headline {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 32px;
        }
        .main-projects__header {
            text-transform: uppercase;
        }
        .main-projects__btn {
            border-color: $main-color;
            color: $main-color;

            &:hover {
                color: #fff;
                background: $main-color;
            }
        }

        .main-projects__lists {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 19px;
            margin-bottom: 50px;
        }
        .main-projects__list {
            position: relative;
            padding: 32px 30px;
            box-sizing: border-box;
            border: 1px solid #DEDEDE;            

            img {
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                z-index: 0;
            }
        }
        .main-projects__item {
            position: relative;
            z-index: 1;
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            
            /* or 28px */
            text-decoration-line: underline;
            
            a {
                color: #343B40;            
            }
        }
    }
    .main-calculate {
        padding: 100px 0 50px;
        box-sizing: border-box;
        img {
            width: 100%;
        }
    }

    .same__slider-wrap {
        position: relative;
    }
    .same__slider {
        padding: 0 55px;
        .slick-track {
            padding-bottom: 11px;
        }
    }
    .same-slider__item {
        margin: 0 6px;
        background: #FFFFFF;
        /* Shadow */
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);            
    }
    .same-slider__item__img-box {
        height: 215px;
        img {
            width: 100%;
            height: inherit;
            object-fit: cover;
        }
    }
    .same-slider__item__desc {
        box-sizing: border-box;
        background: #fff;
        padding: 20px 20px 23px;
        min-height: 145px;

        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }
    .same-slider__item__headline {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .same-slider__item__heading {
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        /* identical to box height, or 20px */
        color: #000000;
    }
    .same-slider__item__square {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        /* or 20px */
        text-align: right;
        color: #000000;
    }
    .same-slider__item__list {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .main-services__item__sublist {
        display: none;
    }
    .same-slider__item__list__item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        img {

        }
        span {
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 140%;
            /* identical to box height, or 14px */
            text-align: center;
            color: #000000;                
        }
    }    

    .main-services {
        padding-bottom: 70px;
        .container {

        }
        .main-services__header {
            text-align: center;
            margin-bottom: 40px;
        }
        .main-services__tabs-wrapper {
            display: grid;
            grid-template-columns: 387px 1fr;
            filter: drop-shadow(0px 0px 20px rgba(0, 0, 0, 0.1));            
        }
        .main-services-list {
            background: #F8F8F8;
        }
        .main-services__item {
            cursor: pointer;
            font-weight: 600;
            font-size: 18px;
            line-height: 22px;
            
            .wrap {
                position: relative;
                padding: 28px 38px;
                display: flex;
                justify-content: start;
                align-items: center;
                color: #343B40;   
                text-transform: uppercase;

                &.--dropdown-arrow {
                    &::after {
                        display: none;
                        border: 7px solid transparent;
                        border-top: 7px solid #000;

                        position: absolute;
                        top: 50%;
                        right: 21px;
                        transform: translateY(-50%);
                        transition: .3s;
                    }
                }
                
            }
            &._active {
                .--dropdown-arrow {
                    &::after {
                        border: 7px solid transparent;
                        border-top: 7px solid #fff !important;
                        transform: translateY(-50%) rotate(180deg);
                    }
                }
            }
            
            .num {
                position: absolute;
                top: 50%;
                left: 15px;
                transform: translateY(-50%);
                color: #B7B7B7; 
                font-size: 70px;   
                opacity: .1;  
                z-index: 0;            
            }

            &._active .wrap {
                background: #fff;

                &::before {
                    content: '';
                    height: 100%;
                    width: 5px;
                    background: $main-color;
                    display: block;

                    position: absolute;
                    top: 0;
                    left: 0;
                }
            }
        }
        .main-services__tabs__container {
            background: #fff;
        }
        .main-services__tabs__content {
            height: 100%;
            &._active {
                display: flex;
            }

            display: none;
            padding: 30px;
            box-sizing: border-box;
            // display: flex;
            flex-direction: column;
            p {
                @include page-subheader();
                color: #343B40;
                font-weight: 300;
                margin-bottom: 30px;
            }
        }
        .main-services__tabs__header {
            font-weight: 700;
            font-size: 32px;
            line-height: 135%;
            display: block;
            margin-bottom: 15px;
        }
        .main-services__tabs__wrapper {
            margin-top: auto;
            display: flex;
            justify-content: start;
            align-items: center;
            gap: 24px;
            margin-top: auto;

        }
        .main-services__tabs__btn {

        }
