            background: transparent;
            position: absolute;
            top: 50%;
            right: 67%;
            transform: translateY(-50%);
            z-index: 10;

            &.main-news__next {
                right: unset;
                left: 67%;
                transform: translateY(-50%) scale(-1);
            }
        }

        .main-news__dots {
            position: relative;
            max-width: 794px;
            width: 100%;
            margin-left: auto;
            margin-right: auto;
            .slick-dots {
                position: static;
                display: flex;
                justify-content: start;
                align-items: center;
                gap: 7px;
            }
            .slick-dots li {
                width: 100%;
                margin: 0;
                height: 3px;
                background: rgba(150, 150, 150, .15);
                transition: .3s;
                position: relative;
                overflow: hidden;


                &.slick-active::before {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: $main-color;
                    width: 100%;
                    height: 100%;
                    animation: stories 5s linear;
                }
            }
            .slick-dots li button {
                display: none;
