    .index-page .index-about .index-about__desc {
        font-weight: 400;
        font-size: 13px;
        line-height: 140%;        
    }
    .index-page .index-about .index-about__item {
        border: none !important;
        padding: 0px !important;
    }
    .index-page .index-about .index-about__item:last-of-type {
        padding: 10px !important;
    }
    .index-page .index-about .index-about__list {
        grid-template-columns: repeat(2, 1fr);
        gap: 18px;
    }
    .index-page .index-about .index-about__img {
        display: none;
    }
    .index-page .index-about .index-about__header {
        text-align: center;
        font-size: 18px;
        line-height: 25px;
        .--main-color {
            display: block;
            font-size: 36px;
            line-height: 50px;           
