        // min-height: unset;
    }
    .index-page .index-title .index-title__bg {
        position: static;
        display: block;
        width: 100%;
        height: 375px;
        object-fit: cover;
        display: none;
        &._mob {
            display: block;
        }
    }
    .index-page .index-title .index-title__slider__dots-wrapper {
        bottom: unset;
        top: 345px;
        right: 50%;
        transform: translateX(50%);
    }
    .index-page .index-title .index-title__header {
        text-align: center;
        margin-bottom: 22px;
        max-width: 260px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    .index-page .index-title .index-title__btns {
        flex-direction: column-reverse;
        align-items: center;
        margin-top: 15px;
    }
    .index-page .index-title .container {
        padding: 0;
        margin-top: 0;
        flex-grow: 1;
        display: flex;
    }
    .index-page .index-title .index-title__desc {
        display: none;
    }
    .index-page .index-title .index-title__text {
        min-height: unset;
        max-width: unset;
        background: #343B40;     
        opacity: 1;
        backdrop-filter: none;  
        
        padding: 29px 60px 25px;
        flex-grow: 1;
    }
    .header .logo {
        max-width: 113px;
    }
    .header .header__bottom,
    .header .header__socials,
    .header .header__btn,
    .header .header__auth {
        display: none;
    }

    .header .header__phone {
        font-weight: 600;
        font-size: 20px;
        line-height: 140%;
    }
    
    .header .burger__wrapper {
        display: block;
    }
}
@media screen and (max-width: 756px) {
    .footer .footer__bot {
        padding: 20px;
    }
    .footer .footer__social {
        position: absolute;
        bottom: 37px;
        left: 50%;
        transform: translateX(-50%);
    }
    .footer .footer__top {
        position: relative;
        padding: 40px 0 87px;
    }
    .footer .footer__phone {
        margin: 23px 0 15px;
    }
    .footer .footer__desc {
        display: none;
    }
    .footer .footer__top__right__wrap {
        gap: 5px;
    }
    .footer .footer__top__left {
        display: flex;
        justify-content: start;
        align-items: center;
        flex-direction: column;
    }
    .footer .footer__controlls {
        align-items: center;
        flex-direction: column-reverse;
    }
    .footer .footer__list__item {
        font-weight: 600;
        font-size: 17px;
        line-height: 180%; 
        text-decoration: none !important;
        &:not(:last-child) {
            margin-bottom: 5px;
        }
        &:first-of-type {
            margin-top: 5px;
        }
    }
    .footer .footer__list__item a {
        display: block;
        text-align: center;
        text-decoration: none !important;
    }
    .footer .footer__header {
        text-align: center;
        font-weight: 600;
        font-size: 17px;
        line-height: 180%;         
    }
    .footer .footer__wrapper {
        flex-direction: column;
        align-items: center;
    }
    .footer .footer__top__right {
        flex-direction: column;
        align-items: center;
    }
}

