        text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);        
    }
    .main-news__controlls {
        // display: none;
    }

    .index-page .main-news .main-news__arrow {
        left: 0;
        // border: 1px solid red;
        height: 70%;
        width: 25%;
        z-index: 1;

        img {
            display: none;
        }
    }
    .index-page .main-news .main-news__slide__link {
        position: relative;
        z-index: 10;
    }
    .index-page .main-news .main-news__arrow.main-news__next {
        right: 0;
        left: unset;
    }
    .index-page .main-news .main-news__slide {
