//Тут храняться миксины

@mixin test() {
    background-color: red;
}

@mixin page-header() {
    display: block;
    font-weight: 700;
    font-size: 45px;
    line-height: 140%;
    text-transform: uppercase;
    color: #343B40;
}
@mixin page-subheader(){
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    
    /* identical to box height, or 28px */
    
    color: #FFFFFF;
}
@mixin page-text(){
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;    
}
@mixin page-field(){
    padding: 10px 10px 9px;
    box-sizing: border-box;
    font-weight: 300;
    font-size: 15px;
    line-height: 140%;
    border: 1px solid #F1F1F1;
    outline: none;    

}

@mixin page-btn() {
    display: inline-block;
    text-align: center;
    padding: 15px 20px;
    text-decoration: none;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    text-transform: uppercase;

    background: $main-color;
    outline: none;
    border: 1px solid $main-color;

    cursor: pointer;

    transition: all .15s ease-in-out;

    &:hover {
        border-color: #DA6536;
        background: #DA6536;
        color: #FFFFFF;
    }

    @media screen and (max-width: 420px) {
        max-width: 100% !important;
        margin-right: 0 !important;
        margin-left: 0 !important;
    }
}

@mixin page-btn-transparent() {
    cursor: pointer;
    display: inline-block;
    // max-width: 357px;
    // width: 100%;
    border: 2px solid #FFFFFF;
    padding: 15px 30px 15px;
    text-decoration: none;

    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    text-transform: uppercase;
    
    color: #FFFFFF;  
    transition: all .15s ease-in-out;

    
    &:hover {
        background: #FFF;
        color: #B34215;
        border: 2px solid #FFF;        
    }
    
    @media screen and (max-width: 420px) {
        max-width: 100% !important;
        margin: 0;
    }
}